// WebSocket connection handler
export const connectWebSocket = async (retries = 3) => {
    let lastError = null;
    let attemptCount = 0;
    
    const connect = async (): Promise<void> => {
      const sKey = await AsyncStorage.getItem('S-KEY');
      if (!sKey) {
        throw new Error('   S-KEY');
      }

      return new Promise<void>((resolve, reject) => {
        let isResolved = false;

        //     
        const connectionTimeout = setTimeout(() => {
          if (!isResolved) {
            webSocketService.removeMessageHandler('key_exchange');
            webSocketService.removeMessageHandler('authorization');
            reject(new Error(`    ( ${attemptCount + 1}  ${retries})`));
          }
        }, 15000);

        //   
        webSocketService.addMessageHandler('key_exchange', async (data: WebSocketMessage) => {
          if (data.key) {
            //    ,  
            webSocketService.removeMessageHandler('key_exchange');
            
            webSocketService.send({
              type: 'authorization',
              action: 'connect',
              S_KEY: sKey
            });
          }
        });

        //  
        webSocketService.addMessageHandler('authorization', async (data: WebSocketMessage) => {
          clearTimeout(connectionTimeout);
          if (!isResolved) {
            isResolved = true;
            if (data.status === 'success' && data.accountData) {
              //   
              await AsyncStorage.setItem('account_data', JSON.stringify(data.accountData));
              
              //   
              webSocketService.send({
                type: 'social',
                action: 'get_profile',
                username: data.accountData.username
              });
              
              webSocketService.removeMessageHandler('authorization');
              resolve();
            } else {
              webSocketService.removeMessageHandler('authorization');
              reject(new Error(data.message || `  ( ${attemptCount + 1}  ${retries})`));
            }
          }
        });

        //    
        webSocketService.send({
          type: 'key_exchange',
          key: 'PUBLIC_KEY' //      
        });
      });
    };
    
    while (attemptCount < retries) {
      try {
        await connect();
        return;
      } catch (error: any) {
        lastError = error;
        attemptCount++;
        console.error(`  WebSocket ${attemptCount}  ${retries}  :`, error.message);
        
        if (attemptCount === retries) {
          throw new Error(`${error.message}\n      VPN`);
        }
        
        //     
        await new Promise(resolve => setTimeout(resolve, 1000 * attemptCount));
      }
    }
};

// Add interceptor to include S-KEY in requests
authApi.interceptors.request.use(async (config) => {
  const sKey = await AsyncStorage.getItem('S-KEY');
  if (sKey) {
    config.headers['S-KEY'] = sKey;
  }
  return config;
});

// Add response error handling
authApi.interceptors.response.use(
  (response) => response,
  async (error) => {
    console.error('API Error:', error?.response?.data || error.message);
    if (error?.response?.status === 401) {
      // Handle unauthorized error (invalid or expired S-KEY)
      await AsyncStorage.removeItem('S-KEY');
      // You might want to trigger a logout here
    }
    return Promise.reject(error);
  }
);

